@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --body-large-medium: Inter;

  /* font sizes */
  --body-large-medium-size: 16px;
  --body-medium-bold-size: 14px;
  --heading-h5-size: 18px;
  --body-small-regular-size: 12px;
  --font-size-4xl-2: 23.2px;
  --heading-h3-size: 24px;
  --font-size-3xs: 10px;
  --font-size-5xs: 8px;
  --font-size-8xl-7: 27.7px;

  /* Colors */
  --greyscale-100: #f1f5f9;
  --greyscale-200: #e2e8f0;
  --greyscale-400: #94a3b8;
  --greyscale-900: #0f172a;
  --color-gray-100: rgba(15, 23, 42, 0.2);
  --color-gray-200: rgba(15, 23, 42, 0.7);
  --primary-600-base: #2563eb;
  --additional-white: #fff;
  --greyscale-500: #64748b;
  --secondary-glamour-pink-500-base: #ed4f9d;
  --color-whitesmoke: #f8fafc;
  --color-honeydew: #e9faef;
  --secondary-algal-fuel-500-base: #24d164;
  --color-lavender-100: #e9effd;
  --primary-100: #dbeafe;
  --secondary-glamour-pink-50: #fdf2f8;
  --color-lavenderblush-100: #fdedf5;
  --greyscale-300: #cbd5e1;
  --additional-sky: #38bdf8;
  --secondary-algal-fuel-50: #f0fdf4;
  --additional-emerald: #34d399;
  --additional-purple: #8b5cf6;

  /* Gaps */
  --gap-13xl: 32px;
  --gap-5xl: 24px;
  --gap-base: 16px;
  --gap-xs: 12px;
  --gap-9xs: 4px;
  --gap-5xs: 8px;
  --gap-11xs: 2px;

  /* Paddings */
  --padding-5xs: 8px;
  --padding-base: 16px;
  --padding-xs: 12px;
  --padding-xl: 20px;
  --padding-5xl: 24px;
  --padding-9xs: 4px;

  /* border radiuses */
  --br-xs: 12px;
  --br-5xs: 8px;
  --br-base: 16px;
  --br-9xs: 4px;
  --br-981xl: 1000px;
  --br-31xl: 50px;
  --br-7xs: 6px;
}
