@keyframes slide-in-top {
  0% {
    transform: translateY(-200px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.background {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-base);
  background-color: var(--additional-white);
  width: 613px;
  height: 445px;
}
.line {
  position: absolute;
  top: 340.5px;
  left: -0.5px;
  border-top: 1px solid var(--greyscale-100);
  box-sizing: border-box;
  width: 614px;
  height: 1px;
}
.background1 {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-base) var(--br-base) 0 0;
  background-color: var(--additional-white);
  width: 613px;
  height: 73px;
}
.shipping1 {
  position: absolute;
  top: 24px;
  left: 24px;
  letter-spacing: 0.2px;
  line-height: 140%;
}
.line1 {
  top: 72.5px;
  left: -0.5px;
  border-top: 1px solid var(--greyscale-100);
  box-sizing: border-box;
  width: 614px;
  height: 1px;
}
.header,
.icon,
.line1 {
  position: absolute;
}
.icon {
  top: 18px;
  left: 549px;
  border-radius: var(--br-5xs);
  width: 40px;
  height: 40px;
  overflow: hidden;
}
.header {
  top: 0;
  left: 0;
  width: 613px;
  height: 73px;
}
.button1 {
  position: relative;
  letter-spacing: 0.2px;
  line-height: 140%;
}
.buttonlargesecondary {
  flex: 1;
  border-radius: var(--br-xs);
  background-color: var(--additional-white);
  border: 1px solid var(--greyscale-200);
  box-sizing: border-box;
  height: 56px;
  padding: var(--padding-5xs);
  align-items: center;
  justify-content: center;
}
.button,
.buttonlargeprimary,
.buttonlargesecondary {
  display: flex;
  flex-direction: row;
}
.buttonlargeprimary {
  flex: 1;
  border-radius: var(--br-xs);
  background-color: var(--primary-600-base);
  height: 56px;
  padding: var(--padding-5xs);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: var(--additional-white);
}
.button {
  position: absolute;
  top: 365px;
  left: 24px;
  width: 565px;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
  font-size: var(--body-large-medium-size);
}
.text {
  align-self: stretch;
  position: relative;
  line-height: 160%;
}
.please-fill-in {
  flex: 1;
  position: relative;
  letter-spacing: 0.2px;
  line-height: 150%;
  font-weight: 500;
}
.brand,
.inputstandardfill {
  display: flex;
  justify-content: flex-start;
}
.inputstandardfill {
  align-self: stretch;
  border-radius: var(--br-xs);
  border: 1px solid var(--greyscale-200);
  box-sizing: border-box;
  height: 56px;
  flex-direction: row;
  padding: var(--padding-5xs) var(--padding-base);
  align-items: center;
  font-size: var(--body-large-medium-size);
  color: var(--greyscale-400);
}
.brand {
  flex: 1;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-base);
}
.chevron-down-icon {
  position: relative;
  width: 20px;
  height: 20px;
  overflow: hidden;
  flex-shrink: 0;
}
.inputstandardfill1 {
  align-self: stretch;
  border-radius: var(--br-xs);
  border: 1px solid var(--greyscale-200);
  box-sizing: border-box;
  height: 56px;
  flex-direction: row;
  padding: var(--padding-5xs) var(--padding-base);
  align-items: center;
  gap: var(--gap-xs);
  font-size: var(--body-large-medium-size);
  color: var(--greyscale-400);
}
.content,
.inputstandardfill1,
.select {
  display: flex;
  justify-content: flex-start;
}
.select {
  width: 565px;
  flex-direction: row;
  align-items: center;
  gap: var(--gap-5xl);
}
.content {
  position: absolute;
  top: 97px;
  left: 24px;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-13xl);
  font-size: var(--body-medium-bold-size);
}
.modal-shipping {
  position: absolute;
  top: 0;
  left: 0;
  width: 613px;
  height: 445px;
}
.shipping {
  position: relative;
  width: 613px;
  height: 445px;
  opacity: 0;
  max-width: 100%;
  max-height: 100%;
  overflow: auto;
  text-align: left;
  font-size: var(--heading-h5-size);
  color: var(--greyscale-900);
  font-family: var(--body-large-medium);
}
.shipping.animate {
  animation: 0.25s ease 0s 1 normal forwards slide-in-top;
}
